h3 {
	text-align: center;
}

div.attacker, div.defender, div.result {
	position: relative;
	float: left;
	padding: 10px;
	margin: 5px;
}

#attacker {
	font-family: "Fire Emblem Heroes";
	border: 2px solid blue;
}

#defender {
	font-family: "Fire Emblem Heroes";
	border: 2px solid red;
}

.result {
	border: 1px solid green;
	width: 431px;
	text-align: center;
}

@font-face {
	font-family: "Fire Emblem Heroes";
	src: url(./feh.ttf) format("ttf");
}

